'use client';
import { useEffect, useRef, useState } from 'react';
import Link from 'next/link';

export default function Page() {
  const demoRef = useRef<HTMLDivElement>(null);
  const [tableHtml, setTableHtml] = useState('');

  useEffect(() => {
    setTimeout(() => {
      console.log('111');
    }, 2000);
    console.log('222');

    function getSomething() {
      return new Promise<void>((resolve) => {
        setTimeout(() => {
          console.log('1111');
          resolve();
        }, 1000);
      });
    }
    getSomething().then(() => {
      console.log('2222');
    });

    async function getSomething2() {
      await new Promise<void>((resolve) => {
        setTimeout(() => {
          console.log('111111');
          resolve();
        }, 1000);
      });
    }
    (async () => {
      await getSomething2();
      console.log('222222');
    })();

    async function getAllCommitDates(owner: string, repo: string) {
      const url = `https://api.github.com/repos/${owner}/${repo}/commits`;
      try {
        const response = await fetch(url);
        if (!response.ok) {
          throw new Error('网络响应失败');
        }
        const commits = await response.json();
        // 提取每个提交的日期
        const dates = commits.map((commit: any) => commit.commit.author.date);
        return dates;
      } catch (error) {
        console.error('获取提交日期失败:', error);
        return [];
      }
    }

    function showCommitDatesTable(dates: string[]) {
      let html = '<table class="w-full text-left border-collapse chinese-border text-[var(--foreground)]">';
      html += '<thead><tr class="bg-black/80"><th class="p-2 border border-[var(--border)]">序号</th><th class="p-2 border border-[var(--border)]">提交日期</th></tr></thead><tbody>';
      dates.forEach((date, idx) => {
        html += `<tr><td class="p-2 border border-[var(--border)]">${idx + 1}</td><td class="p-2 border border-[var(--border)]">${date}</td></tr>`;
      });
      html += '</tbody></table>';
      setTableHtml(html);
    }

    getAllCommitDates('yangjh-xbmu', 'Web-develop')
      .then(dates => {
        console.log('所有提交日期:', JSON.stringify(dates));
        showCommitDatesTable(dates);
      });
  }, []);

  return (
    <div className="flex flex-col min-h-screen bg-[var(--background)] text-[var(--foreground)] relative overflow-x-hidden">
      {/* 祥云装饰 */}
      <div className="chinese-cloud" style={{top: 24, left: 32}}>
        <svg viewBox="0 0 80 40" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M10 30 Q 20 10, 40 20 T 70 25" stroke="#e7b85a" strokeWidth="3" fill="none"/>
        </svg>
      </div>
      <main className="flex-grow container mx-auto px-4 py-12 z-10">
        <div className="relative mb-12">
          <Link href="/" className="absolute -top-8 left-0 flex items-center gap-2 text-[var(--border)] hover:text-[var(--accent)] transition-colors font-bold text-xl" style={{fontFamily: 'var(--font-chinese)'}}>
            <span className="text-2xl">←</span>
            <span>返回首页</span>
          </Link>
        </div>
        <section className="max-w-3xl mx-auto mb-10 chinese-border bg-black/70 p-8">
          <h2 className="text-2xl font-bold mb-4 text-[var(--border)]" style={{fontFamily: 'var(--font-chinese)'}}>案例说明：异步编程与API数据获取</h2>
          <ul className="list-disc list-inside text-[var(--accent)] space-y-2 mb-2" style={{fontFamily: 'var(--font-chinese)'}}>
            <li>本案例演示了 setTimeout、Promise、async/await、fetch API、DOM 操作等异步编程基础。</li>
            <li>页面加载后会自动获取 GitHub 提交日期并渲染表格。</li>
            <li>请按 F12 打开浏览器控制台，观察输出。</li>
          </ul>
        </section>
        <section className="max-w-3xl mx-auto chinese-border bg-black/60 p-8 flex flex-col items-center">
          <h1 className="text-3xl font-bold mb-6 text-center text-[var(--border)]" style={{fontFamily: 'var(--font-chinese)'}}>JavaScript 异步编程练习</h1>
          <div id="demo" ref={demoRef} className="w-full overflow-x-auto mt-4" dangerouslySetInnerHTML={{ __html: tableHtml }} />
        </section>
      </main>
      {/* 古建筑底部装饰 */}
      <div className="chinese-building">
        <svg viewBox="0 0 220 90" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="30" y="60" width="160" height="20" fill="#bfa76a" stroke="#e7b85a" strokeWidth="3"/>
          <path d="M30 60 Q 110 10, 190 60" stroke="#e7b85a" strokeWidth="5" fill="none"/>
          <rect x="80" y="40" width="60" height="20" fill="#bfa76a" stroke="#e7b85a" strokeWidth="2"/>
          <path d="M80 40 Q 110 20, 140 40" stroke="#e7b85a" strokeWidth="3" fill="none"/>
        </svg>
      </div>
    </div>
  );
} 